var OriginalEmailFunction = Email.send;

Email.send = function (options) {
    
    // Wrap and prep
    var instance      = new OriginalEmailFunction(options);
    options.timestamp = new Date();
    options.unread    = true;

   	// Insert to database
    Package['meteortoys:toykit'].MeteorToysData["Email"].insert(options);

    // Boom
    return instance;

};